/**
 * Mocking client-server processing
 */
const _products = [
  {"id": 1, "title": "Cerveza Heineken 1 L", "price": 220, "inventory": 2, "img": "data:image/jpeg;base64,"},
  {"id": 2, "title": "Fernet Branca 750 ml", "price": 550, "inventory": 10, "img": 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSuxbdCzPK4FEaNcskboUntdnYp-suAhDWa_sGbNuUShDoMYwDxeFZBKoF0DQf7RwsbrRIWyNI&usqp=CAc'},
  {"id": 3, "title": "Coca-Cola 2.25 L", "price": 100, "inventory": 5, "img": "https://http2.mlstatic.com/D_NQ_NP_933399-MLA45307671863_032021-O.webp"}
]

export default {
  getProducts (cb) {
    setTimeout(() => cb(_products), 100)
  },

  buyProducts (products, cb, errorCb) {
    setTimeout(() => {
      // simulate random checkout failure.
      (Math.random() > 0.5 || navigator.userAgent.indexOf('PhantomJS') > -1)
        ? cb()
        : errorCb()
    }, 100)
  }
}


